@use "." as this;
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Position";

#{this.$selector} {
    @include Flex.set($dir: column, $justify: center, $align: center);

    #{Text.$selector}.title {
        @include Font.set($size: delta, $weight: normal, $family: "heading");
        @include Margin.set($bottom: alpha);
    }

    > div {
        @include Flex.set($dir: column);

        .dropdown {
            @include Margin.set($bottom: alpha);
            #{Button.$selector}.dropdownLabel {
                @include Flex.set($align: center);
                @include Padding.set($preset: alpha);
                cursor: pointer;
            }

            > ul {
                @include Margin.set($top: alpha);
                @include Position.set($left: none, $right: none);

                > li {
                    @include Position.set($position: relative, $preset: zero);
                    @include Margin.set($preset: auto);
                }

                #{Button.$selector} {
                    @include Padding.set($preset: alpha);
                    @include Color.set($bg: "transparent");
                    @include Border.set($style: none);
                }
            }
        }
        #{Button.$selector} {
            #{Text.$selector} {
                @include Font.set($align: center, $transform: capitalize);
            }
        }
    }

    @include screen.lg {
        @include Flex.set($dir: row, $justify: space-between);

        > div {
            @include Flex.set($dir: row);
            .dropdown {
                @include Margin.set($right: alpha);
            }
        }
    }

    @include screen.xl {
        @include Margin.set($right: beta, $left: beta);
        #{Text.$selector}.title {
            @include Font.set($size: iota);
        }
    }
}
