@use "." as this;
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Form/InputDateField";
@use "@app/bancor-components/Element/Form/InputSelect";
@use "@app/bancor-components/Element/Form/InputNumber";
@use "@app/bancor-components/Element/Form/Input";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Margin";

#{this.$selector} {
    @include Margin.set($bottom: delta);
    .filtersHeader {
        @include Flex.set($justify: between);
        width: 100%;
        #{Text.$selector} {
            @include Margin.set($top: alpha, $bottom: beta);
            @include Font.set($size: delta, $weight: normal, $family: "heading");
        }
        #{Button.$selector} {
            @include Button.asUnderlineLink;
        }
    }
    #{Input.$selector},
    #{InputSelect.$selector},
    #{InputNumber.$selector},
    #{InputDateField.$selector} {
        @include Margin.set($top: alpha, $bottom: alpha);
    }

    .accordion {
        @include Margin.set($top: delta);
        .accordionSummary {
            #{Text.$selector} {
                @include Font.set($size: gamma, $weight: normal, $family: "heading");
            }
        }
        .accordionDetails {
            @include Margin.set($top: beta);
        }
    }

    @include screen.lg {
        > ul {
            @include Display.set($display: grid);
            grid-template-columns: minmax(300px, 1fr) minmax(300px, 1fr);
            grid-auto-flow: dense;
            grid-template-areas:
                "dateFields dateFields"
                "paymentMethod paymentStatus"
                "echeqInfo echeqInfo"
                "debitAccount paymentReference";
            .dateFields {
                @include Display.set($display: grid);
                grid-template-columns: inherit;
                grid-area: dateFields;
            }
            .echeqInfo {
                @include Margin.set($preset: alpha);
                grid-area: echeqInfo;
            }
            .paymentMethod {
                grid-area: paymentMethod;
            }
            .debitAccount {
                grid-area: debitAccount;
            }
            .paymentReference {
                grid-area: paymentReference;
            }
            .paymentStatus {
                grid-area: paymentStatus;
            }
            #{Input.$selector},
            #{InputSelect.$selector},
            #{InputNumber.$selector},
            #{InputDateField.$selector} {
                @include Margin.set($left: alpha, $right: alpha);
            }
        }
        .accordion {
            .accordionDetails {
                @include Display.set($display: grid);
                grid-template-columns: 1fr 1fr;
                #{Input.$selector},
                #{InputSelect.$selector},
                #{InputNumber.$selector},
                #{InputDateField.$selector} {
                    @include Margin.set($left: alpha, $right: alpha);
                }
            }
        }
    }
}
